<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
      <ul class="navbar-nav">
          <span class="navbar-brand mb-0 h1">{{title}}</span>
          <li class="nav-item" v-for="item in items" :key="item" :class="{active: item === selectedItem}">
              <a class="nav-link"
                 href="#"
                 @click="selectMenuItem(item)">{{item}}</a>
          </li>
      </ul>
  </nav>
</template>

<script>
import {mapState, mapMutations} from 'vuex'
export default {
  computed: {
    ...mapState({
      'items': state => state.menu.items,
      'selectedItem': state => state.menu.selectedItem,
      'title': state => state.menu.title
    })
  },
  methods: {
    ...mapMutations(['selectMenuItem'])
  }
}
</script>
