import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import shuffle from './shuffle'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    tileUrl: 'https://api.mapbox.com/styles/v1/fdlk/cjihuy20m13s52rpckef61fvy/tiles/256/{z}/{x}/{y}?access_token=pk.eyJ1IjoiZmRsayIsImEiOiJjamlod21xOG8xN3h5M3BuN21xYTZpcjFmIn0.BiQvMPCQUITFffa0PfCuBA',
    menu: {
      title: 'Topo voor Toto',
      items: ['toevoegen', 'kijken', 'oefenen'],
      selectedItem: 'kijken'
    },
    apikey: 'AIzaSyAzzieBizM7JzO8cO1jWNwZG0ExOPnwG24',
    newPoi: null,
    pois: [
      {'name': 'Amstelveen', 'latlng': [52.301944, 4.858056]},
      {'name': 'Purmerend', 'latlng': [52.505, 4.964167]},
      {'name': 'Den Haag', 'latlng': [52.095556, 4.316389]},
      {'name': 'Alphen aan de Rijn', 'latlng': [52.128306, 4.653806]},
      {'name': 'Delft', 'latlng': [52.011111, 4.3575]}, {
        'name': 'Gouda',
        'latlng': [52.018056, 4.705556]
      }, {'name': 'Leiden', 'latlng': [52.157222, 4.4925]}, {
        'name': 'Zoetermeer',
        'latlng': [52.060669, 4.494025]
      }, {'name': 'Spijkenisse', 'latlng': [51.85615019999999, 4.2972181]}, {
        'name': 'Schiedam',
        'latlng': [51.91695989999999, 4.398818599999999]
      }, {'name': 'Vlaardingen', 'latlng': [51.9120668, 4.3494366]}, {
        'name': 'Dordrecht',
        'latlng': [51.81329789999999, 4.6900929]
      }, {'name': 'Bergen op Zoom', 'latlng': [51.49457580000001, 4.2871622]}, {
        'name': 'Roosendaal',
        'latlng': [51.535849, 4.465321299999999]
      }, {'name': 'Breda', 'latlng': [51.5719149, 4.768323]}, {
        'name': 'Tilburg',
        'latlng': [51.560596, 5.0919143]
      }, {'name': 'Noordzee', 'latlng': [56.511018, 3.515625]}, {
        'name': 'Waddenzee',
        'latlng': [53.5786884, 6.9495078]
      }, {'name': 'Haarlemmermeer', 'latlng': [52.3003784, 4.6743594]}, {
        'name': 'Zaanstad',
        'latlng': [52.4579659, 4.7510425]
      }, {'name': 'Rijnmond', 'latlng': [51.84465, 4.38096]}, {
        'name': 'Nationaal Park De Biesbosch',
        'latlng': [51.7507546, 4.763230699999999]
      }],
    selectedPoi: null,
    message: '',
    messageStyle: null,
    oefenen: [],
    goed: 0,
    fout: 0
  },
  mutations: {
    addPoi (state) {
      state.pois.push(state.newPoi)
      state.selectedPoi = state.newPoi.name
      state.newPoi = null
    },
    setNewPoi (state, newPoi) {
      state.newPoi = newPoi
      state.message = ''
    },
    selectPoi (state, poi) {
      if (state.menu.selectedItem === 'oefenen') {
        if (state.selectedPoi === poi) {
          state.message = `Ja daar ligt ${poi}!`
          state.messageStyle = 'success'
          state.goed++
        } else {
          state.message = `Nee, dat was ${poi}!`
          state.messageStyle = 'danger'
          state.fout++
        }
        state.oefenen.splice(0, 1)
        if (state.oefenen.length) {
          state.selectedPoi = state.oefenen[0]
        } else {
          state.selectedPoi = null
        }
      } else {
        state.selectedPoi = poi
      }
    },
    removePoi (state, index) {
      state.pois.splice(index, 1)
    },
    selectMenuItem (state, item) {
      state.menu.selectedItem = item
      state.message = ''
      if (item === 'oefenen') {
        state.goed = 0
        state.fout = 0
        const poiNames = state.pois.map(poi => poi.name)
        shuffle(poiNames)
        state.oefenen = poiNames
        state.selectedPoi = state.oefenen[0]
      }
    }
  },
  actions: {
    findPoi ({commit, state}, address) {
      axios.get(`https://maps.googleapis.com/maps/api/geocode/json?&address=${address}&key=${state.apikey}`)
        .then(response => {
          if (response.data && response.data.results && response.data.results[0] && response.data.results[0].geometry && response.data.results[0].geometry.location) {
            const name = response.data.results[0].address_components[0].short_name
            const latlng = [response.data.results[0].geometry.location.lat, response.data.results[0].geometry.location.lng]
            commit('setNewPoi', {name, latlng})
          } else {
            console.log('error looking up location', response)
            state.message = `Ik kan ${address} niet vinden.`
            state.messageStyle = 'danger'
          }
        })
    }
  },
  getters: {
    circles (state) {
      const result = state.pois.map(poi => ({
        ...poi,
        color: (poi.name === state.selectedPoi && state.menu.selectedItem !== 'oefenen') ? 'red' : 'black'
      }))
      if (state.newPoi && state.newPoi.latlng) {
        result.push({...state.newPoi, color: 'red'})
      }
      return result
    }
  }
})
