<template>
  <div>
    <h2>Plaatsen toevoegen</h2>
    <h3>Zoeken</h3>
    <div class="form-group">
      <label for="name">Wat wil je opzoeken?</label>
      <input type="text" v-model="name" class="form-control" id="name" aria-describedby="emailHelp" placeholder="Vul hier de naam in">
      <small id="nameHelp" class="form-text text-muted">Hier kun je een plaats opzoeken.</small>
    </div>
    <button type="button" class="btn btn-info" @click.stop="findPoi(name)">Zoek</button>
    <div v-if="newPoi && newPoi.latlng">
      <h3 class="pt-3">Toevoegen</h3>
      <div>
        {{newPoi.name}} gevonden!
      </div>
      <button type="button" class="btn btn-info" @click.stop="addPoi">Voeg toe</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex'
import { LMap, LCircleMarker, LTileLayer, LPopup } from 'vue2-leaflet'
import 'leaflet/dist/leaflet.css'
export default {
  components: {
    LMap,
    LTileLayer,
    LCircleMarker,
    LPopup
  },
  data () {
    return {
      name: ''
    }
  },
  computed: {
    ...mapState(['newPoi'])
  },
  methods: {
    ...mapActions(['findPoi']),
    ...mapMutations(['addPoi'])
  }
}
</script>

<style scoped>
map {
    width: 600px;
    height: 400px;
}
</style>
