<template>
  <div>
    <h2>Plaatsen</h2>
    <ul>
      <li v-for="(poi, index) in pois" :key="poi.name">
        <a @click="selectPoi(poi.name)" href="#" :class="{selected: poi.name === selectedPoi}">{{poi.name}}</a>
        &nbsp;<a @click="removePoi(index)" href="#" v-if="selectedMenuItem === 'toevoegen'"><i class="fa fa-close"></i></a>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  computed: {
    ...mapState({'selectedMenuItem': state => state.menu.selectedItem}),
    ...mapState(['pois', 'selectedPoi'])
  },
  methods: {
    ...mapMutations(['selectPoi', 'removePoi'])
  }
}
</script>

<style scoped>
  a.selected {
    font-weight: bold;
  }
</style>
