<template>
    <l-map class="topomap" :zoom.sync="zoom" :center="center">
        <l-tile-layer :url="tileUrl" :attribution="attribution"></l-tile-layer>
        <l-circle-marker v-for="circle in circles"
                         :lat-lng="circle.latlng"
                         :key="circle.name"
                         :color="circle.color"
                         :fillColor="circle.color"
                         :fillOpacity="0.6"
                         :radius="radius"
                         @click="selectPoi(circle.name)">
          <l-tooltip v-if="selectedMenuItem !== 'oefenen'">{{circle.name}}</l-tooltip>
        </l-circle-marker>
    </l-map>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import { LMap, LCircleMarker, LTileLayer, LTooltip } from 'vue2-leaflet'
export default {
  components: {
    LMap,
    LTileLayer,
    LCircleMarker,
    LTooltip
  },
  data () {
    return {
      attribution: 'Topo voor Toto, gemaakt met MapBox en OSM data',
      center: [52.2, 5.50],
      zoom: 7
    }
  },
  computed: {
    ...mapGetters(['circles']),
    ...mapState(['tileUrl']),
    ...mapState({'selectedMenuItem': state => state.menu.selectedItem}),
    radius () {
      if (this.zoom >= 10) {
        return 10
      }
      if (this.zoom <= 5) {
        return 1
      }
      return {
        9: 7,
        8: 6,
        7: 5,
        6: 2
      }[this.zoom]
    }
  },
  methods: {
    ...mapMutations(['selectPoi'])
  }
}
</script>

<style scoped>
.topomap {
    width: 600px;
    height: 600px;
}
</style>
